import React, { Component } from 'react';
import { Switch, Route } from 'react-router-dom'
import './App.css';

import MainIndex from './components/MainIndex'
import SimpleSchedule from './components/sideProject/SimpleSchedule'

class App extends Component {
  render() {
    return (
      <div className="App">
        <Switch>
          <Route path='/sidepj/schedule' component={SimpleSchedule} />
          <Route path='/' component={MainIndex} />
          
        </Switch>
      </div>
    );
  }
}

export default App;
