import React, { Component } from 'react'
import './SimpleSchedule.css'

let wW = window.innerWidth
window.onresize = function(){

}
let operators = []
for(let i = 0; i < 6; i++){
    operators.push("Operator" + (i + 1))
}

let startTime = 800
let endTime = 2000

class TimeFrame {
    constructor(startStr, endStr){
        this.start = startStr;
        this.end = endStr;
        this.frame = (() => {
            let newFrame = {}
            let currentTime = this.start
            let i = 0
            while(currentTime <= this.end){
                newFrame[currentTime] = i
                newFrame[i] = currentTime
                currentTime = TimeFrame.next(currentTime)
                
                i++
                if(currentTime === false){
                    console.log("TimeFrame get next time error")
                    break
                }
            }
            return newFrame
        })();
        this.getSpan = function(startT, endT){
            let currentT
            let startIdx = this.frame[startT]
            let spanCounter = 0
            while(this.frame[startIdx] !== undefined){
                currentT = this.frame[startIdx]
                
                if(currentT == endT){
                    break
                }
                spanCounter++
                startIdx++
            }
            return spanCounter
        }
    }
    static last = function(str){
        if(str.length < 3){
            return false
        }
        let strValue = TimeFrame.getValue(str)
        strValue -= 50
        let newStr = TimeFrame.getName(strValue)
        return newStr
    }
    static next = function(str){
        if(str.length < 2){
            return false
        }
        let strValue = TimeFrame.getValue(str)
        strValue += 50
        let newStr = TimeFrame.getName(strValue)
        return newStr
    }
    static getValue = function(str){
        let newStr = str
        if(newStr[newStr.length-2] === "3"){
            let strArr = newStr.split("")
            strArr[newStr.length-2] = "5"
            newStr = strArr.join("")
        }
        return parseInt(newStr)
    }
    static getName = function(str){
        let newStr = str.toString()
        if(newStr[newStr.length-2] === "5"){
            let strArr = newStr.split("")
            strArr[newStr.length-2] = "3"
            newStr = strArr.join("")
        }
        return newStr
    }
}

let timeFrame = new TimeFrame(startTime, endTime)


let colWidth = 150
function initTable(cols) {
    colWidth = Math.floor(wW * 0.95 / cols)
}

class SimpleSchedule extends Component {
    constructor(props){
        super(props)
        this.state = {
            operators: [],
            timeFrame: new TimeFrame(0, 0),
            operatorSchedule: {},
            modifierStatus: 'hidden',
            currentTimeframe: {
                user: '',
                note: '',
                startHour: 8,
                startMinute: '00',
                endHour: 8,
                endMinute: '00',
                isEmpty: true,
                frameColor: '#ff8000',
                backupStartTime: [],
                backupEndTime: [],
            }
        }
        this.handleCancel = this.handleCancel.bind(this)
        this.handleCellClick = this.handleCellClick.bind(this)
        this.handleDelete = this.handleDelete.bind(this)
        this.handleAdd = this.handleAdd.bind(this)
        this.handleEdit = this.handleEdit.bind(this)
        this.deleteAppointment = this.deleteAppointment.bind(this)
        this.createAppointment = this.createAppointment.bind(this)
        this.handleInputChange = this.handleInputChange.bind(this)
        this.handleSelectChange = this.handleSelectChange.bind(this)
    }

    componentDidMount(){
        let operatorSchedule = {}
        for(let i = 0; i < operators.length; i++){
            operatorSchedule[operators[i]] = {}
            // console.log("operatorSchedule[operator] ", operators[i])
            let j = 0
            while(timeFrame.frame[j]){
                
                operatorSchedule[operators[i]][timeFrame.frame[j]] = "Avaliable"
                // console.log("Start while loop ", timeFrame.frame[j])
                j++
            }
        }
        // console.log("Component did mount", operatorSchedule["operator1"])
        operatorSchedule["Operator1"]['900'] = {startTime: '9:00', endTime: '11:00', user: 'Adam', note: 'Regular Flight', color: '#ff8000'}
        operatorSchedule["Operator1"]['930'] = null
        operatorSchedule["Operator1"]['1000'] = null
        operatorSchedule["Operator1"]['1030'] = null

        this.setState({
            operators: operators,
            timeFrame: timeFrame,
            operatorSchedule: operatorSchedule,
        })

        initTable(operators.length+1)
    }

    handleCancel(evt){
        this.setState({...this.state, modifierStatus: 'hidden' })
    }

    handleCellClick(evt){
        // console.log("Event key ", evt.target)
        if(this.state.modifierStatus === 'hidden'){
            // console.log("Get timeframe ", evt.target.getAttribute('timeframe'))
            // when click on the childen of cell, the div, it will triger the parent event, check & if it is from children, get the parent
            let elm = evt.target
            if(evt.target.getAttribute('class') === 'cellText'){
                elm = evt.target.parentElement
            }
            let timeframe = elm.getAttribute('timeframe').toString()
            let startHour = timeframe.slice(0, -2)
            let startMinute = timeframe.slice(-2, timeframe.length)
            let operator =  elm.getAttribute('operator')
            let mostCurrentTimeframe = {
                user: '',
                note: '',
                // startTime: evt.target.getAttribute('timeFrame'),
                startHour: startHour,
                startMinute: startMinute,
                endHour: startHour,
                endMinute: startMinute,
                operator: operator,
                isEmpty: true,
                frameColor: '#ff8000',
                backupStartTime: [],
                backupEndTime: [],
            }
            // console.log("evt.target.operator ", evt.target.getAttribute('timeFrame'))
            // console.log("mostCurrentTimeframe ", mostCurrentTimeframe)
            let { operatorSchedule } = this.state
            // console.log(operatorSchedule[operator][timeframe])
            let appointmentInfo = operatorSchedule[operator][timeframe]
            if(typeof(appointmentInfo) === 'object'){
                let { startTime, endTime, user, note, color } = appointmentInfo
                startTime = startTime.split(':')
                endTime = endTime.split(':')
                mostCurrentTimeframe.startHour = startTime[0]
                mostCurrentTimeframe.startMinute = startTime[1]
                mostCurrentTimeframe.backupStartTime = startTime
                mostCurrentTimeframe.endHour = endTime[0]
                mostCurrentTimeframe.endMinute = endTime[1]
                mostCurrentTimeframe.backupEndTime = endTime
                mostCurrentTimeframe.user = user
                mostCurrentTimeframe.note = note
                mostCurrentTimeframe.isEmpty = false
                mostCurrentTimeframe.frameColor = color
                // console.log(mostCurrentTimeframe)
            }
            this.setState({...this.state, modifierStatus: 'visible', currentTimeframe: mostCurrentTimeframe })
        } else {
            this.setState({...this.state, modifierStatus: 'hidden' })
        }
    }

    handleDelete(evt){
        let { currentTimeframe } = this.state
        let operator = currentTimeframe.operator
        let startTime = [currentTimeframe.startHour, currentTimeframe.startMinute]
        let endTime = [currentTimeframe.endHour, currentTimeframe.endMinute]
        // console.log("Running delete: ", operator, startTime, endTime)
        let response = this.deleteAppointment(operator, startTime, endTime)
        if(!response){
            alert("Delete failed, the time you choose has not appointment.")
        }
        this.handleCancel()
    }

    handleEdit(evt){
        // delete first, then create new. keep the old data before delete, if create fails, roll back, recreate the old appointment
        let { operator, startHour, startMinute, endHour, endMinute, user, note, backupStartTime, backupEndTime } = this.state.currentTimeframe
        let oldStartTime = backupStartTime
        let oldEndTime = backupEndTime
        let newStartTime = [startHour, startMinute]
        let newEndTime = [endHour, endMinute]
        // console.log("Inside edit, old start & end: ", oldStartTime, oldEndTime)
        if(parseInt(startHour) > endHour || (startHour === endHour && parseInt(startMinute) >= endMinute)){
            alert("Appointment end time cannot be less than start time.")
            return false
        }
        let response = this.deleteAppointment(operator, oldStartTime, oldEndTime)
        if(!response){
            alert("Strange thing happened...")
        } else {
            // console.log("Inside edit, new start & end: ", newStartTime, newEndTime)
            if(!this.createAppointment(operator, newStartTime, newEndTime, user, note)){
                alert("Edit appointment failed, timeframe is taken.")
                this.createAppointment(operator, oldStartTime, oldEndTime, user, note)
            } else {
                this.handleCancel()
            }
        }
        
    }

    handleAdd(evt){
        let { operator, startHour, startMinute, endHour, endMinute, user, note } = this.state.currentTimeframe
        // console.log("Adding new schedule with ", operator)
        if(user.split(' ').join('').length === 0){
            alert("Please fill in user name")
        } else {
            if(parseInt(startHour) > endHour || (startHour === endHour && parseInt(startMinute) >= endMinute)){
                alert("Appointment end time cannot be less than start time.")
                return false
            }
            let startTime = [startHour, startMinute]
            let endTime = [endHour, endMinute]
            if(!this.createAppointment(operator, startTime, endTime, user, note)){
                alert("Create appointment failed, timeframe is taken.")
            } else {
                this.handleCancel()
            }
        }
        
    }

    deleteAppointment(operator, startTime, endTime){
        // find the startTime for the operator, if startTime does not have start info, return fail
        let { timeFrame, operatorSchedule } = this.state
        // console.log('In delete, startTime & endTime ', startTime, endTime)
        startTime = startTime.join('')
        if(typeof(endTime) === 'object'){
            endTime = endTime.join('')
        }
        let appointmentInfo = operatorSchedule[operator][startTime]
        // console.log("delete appointment ", appointmentInfo)
        if(typeof(appointmentInfo) === 'object'){
        //{startTime: '900', endTime: '1100', user: 'Adam', note: 'Regular Flight'}
        
            if(appointmentInfo.endTime.split(':').join('') === endTime){
                let timeIdx = timeFrame.frame[startTime]
                endTime = TimeFrame.last(endTime)
                // console.log("delete appointment ", appointmentInfo)
                while(timeFrame.frame[timeIdx]){
                    // console.log('check time ', timeFrame.frame[timeIdx])
                    if(parseInt(timeFrame.frame[timeIdx]) > endTime){
                        // console.log(timeFrame.frame[timeIdx], ' is biggere than ', endTime, ' is ', timeFrame.frame[timeIdx] > endTime)
                        break
                    }
                    operatorSchedule[operator][timeFrame.frame[timeIdx]] = 'Avaliable'
                    timeIdx++
                }
            }
            return appointmentInfo
        } else {
            return false
        }
    }

    createAppointment(operator, startTime, endTime, user, note){
        // find the start time and end time of the operator, if both are not taken, start create, check every step avalibility, else return fail
        let { timeFrame, operatorSchedule } = this.state
        let convertStartTime = startTime.join(':')
        startTime = startTime.join('')
        let convertEndTime = endTime.join(':')
        endTime = endTime.join('')
        // ending in the time does not take the timeframe of the time
        endTime = TimeFrame.last(endTime)
        let startPoint = operatorSchedule[operator][startTime]
        let endPoint = operatorSchedule[operator][endTime]
        // console.log("end point ", endPoint)
        if(startPoint === 'Avaliable' && endPoint === 'Avaliable'){
            let timeIdx = timeFrame.frame[startTime]
            //{startTime: '900', endTime: '1100', user: 'Adam', note: 'Regular Flight'}
            operatorSchedule[operator][timeFrame.frame[timeIdx]] = {startTime: convertStartTime, endTime: convertEndTime, user: user, note: note, color: this.state.currentTimeframe.frameColor}
            timeIdx++
                while(timeFrame.frame[timeIdx]){
                    if(parseInt(timeFrame.frame[timeIdx]) > endTime){
                        break
                    }
                    if(operatorSchedule[operator][timeFrame.frame[timeIdx]] !== 'Avaliable'){
                        console.log("Create failed, roll back ", convertStartTime, timeFrame.frame[timeIdx-1])
                        this.deleteAppointment(operator, convertStartTime, timeFrame.frame[timeIdx-1])
                        return false
                    }
                    operatorSchedule[operator][timeFrame.frame[timeIdx]] = null
                    timeIdx++
                }
            return true
        } else {
            return false
        }
    }

    handleInputChange(evt){
        // console.log(evt.target.id, ' - ', evt.target.value)
        this.setState({...this.state, currentTimeframe: {...this.state.currentTimeframe, [evt.target.id]: evt.target.value }})
    }

    handleSelectChange(evt){
        // console.log(evt.target.id, ' - ', evt.target.value)
        let id = evt.target.id
        let value = evt.target.value
        this.setState({...this.state, currentTimeframe: {...this.state.currentTimeframe, [id]: value} })
    }

    render(){
        let { operators, timeFrame, operatorSchedule, modifierStatus, currentTimeframe } = this.state
        let schedule = []
        let i = 0
        // console.log("currentTimeframe ", currentTimeframe.frameColor)
        while(timeFrame.frame[i]){
            // console.log("Start while loop ", timeFrame.frame[i])
            let currentRow = []
            let currentStatus
            for(let j = 0; j < operators.length; j++){
                // console.log("Start for loop, ", operators[i])
                // console.log("Start for loop, ", operatorSchedule[operators[j]] )
                if(operatorSchedule.hasOwnProperty([operators[j]])){
                    currentStatus = operatorSchedule[operators[j]][timeFrame.frame[i]]
                    let elmKey = `cell-${operators[j]}-${timeFrame.frame[i]}`
                    // console.log("CurrentStatus is ", currentStatus)
                    if(currentStatus === "Avaliable"){
                        currentRow.push(<td 
                                            key={elmKey} 
                                            operator={operators[j]} 
                                            timeframe={timeFrame.frame[i]}
                                            onClick={this.handleCellClick}
                                            ></td>)
                    } else if(currentStatus === null){
                        // Do nothing
                    } else if(typeof(currentStatus) === 'object') {
                        let span = 0
                        //{startTime: '900', endTime: '1100', user: 'Adam', note: 'Regular Flight'}
                        let { startTime, endTime, user, note, color } = currentStatus
                        let convertStartTime = startTime.split(":").join("")
                        let convertEndTime = endTime.split(":").join("")
                        // console.log("currentStatus ", currentStatus)
                        span = this.state.timeFrame.getSpan(convertStartTime, convertEndTime)
                        // console.log("Timespan ", span)
                        let colorStyle = { backgroundColor: color }
                        currentRow.push(
                                            <td 
                                                rowSpan={span} 
                                                // className='orange' 
                                                style={ colorStyle }
                                                key={elmKey} 
                                                operator={operators[j]} 
                                                timeframe={timeFrame.frame[i]}
                                                onClick={this.handleCellClick}>
                                                <div className='cellText'>
                                                {startTime} - {endTime}
                                                <br />
                                                {user}
                                                <br />
                                                {note}
                                                </div>
                                            </td>
                                        )
                    }
                }
            }
            let frame = timeFrame.frame[i].toString()
            let timeTitle = frame.slice(0, -2) + ':' + frame.slice(-2, frame.length)
            schedule.push(<tr><td className='timeTitle' ><div className='timeTitleText'>{timeTitle}</div></td>{currentRow}</tr>)
            i++
        }
        schedule.push(<tr><td className='timeTitle' ><div className='timeTitleText'>20:30</div></td></tr>)

        // console.log("Schedule", schedule)

        let startHoursToSelect = []
        let endHoursToSelect = []
        let timeTitle = []

        let { startHour, startMinute, endHour, endMinute, user, frameColor, note, isEmpty } = currentTimeframe
        for(let i = 8; i < 21; i++){
            // console.log(i, ' & ', endHour)
            if(i == startHour && i == endHour){
                startHoursToSelect.push(<option value={i} selected>{i}</option>)
                endHoursToSelect.push(<option value={i} selected>{i}</option>)
            } else if(i == startHour){
                startHoursToSelect.push(<option value={i} selected>{i}</option>)
                endHoursToSelect.push(<option value={i}>{i}</option>)
            } else if(i == endHour){
                startHoursToSelect.push(<option value={i}>{i}</option>)
                endHoursToSelect.push(<option value={i} selected>{i}</option>)
            } else {
                startHoursToSelect.push(<option value={i}>{i}</option>)
                endHoursToSelect.push(<option value={i}>{i}</option>)
            }
            timeTitle.push(<tr><td>{i}:00</td></tr>)
            timeTitle.push(<tr><td>{i}:30</td></tr>)
        }

        let cellWidthStyle = { width: colWidth+'px' }
        // console.log("container width is ", cellWidthStyle)
        // console.log("colWidth ", colWidth)

        let handleInputChange = this.handleInputChange
        let handleSelectChange = this.handleSelectChange
        let handleAdd = this.handleAdd
        let handleCancel = this.handleCancel
        let handleDelete = this.handleDelete
        let handleEdit = this.handleEdit
        let props = {modifierStatus, 
                    startHoursToSelect, 
                    endHoursToSelect, 
                    startMinute, 
                    endMinute, 
                    user,
                    frameColor,
                    note,
                    isEmpty, 
                    handleInputChange, 
                    handleSelectChange,
                    handleAdd,
                    handleCancel,
                    handleDelete,
                    handleEdit
                }

        return (
            <div id='pageContainer'>
                <h1>Flight Schedule</h1>
                <div id='scheduleContainer'>
                    <table id='scheduleTable' cellSpacing='0px' cellPadding='0px' border='0px'>
                        <tbody>
                            <tr>
                            <th className='timeTitle'></th>
                                {this.state.operators.map((operator) => {
                                    return <th style={cellWidthStyle}>{operator}</th>
                                })}
                            </tr>
                            {schedule}
                        </tbody>
                    </table>
                    <ScheduleModifier props={props}/>
                </div>
            </div>
        )
    }
}


const ScheduleModifier = props => {
    const { modifierStatus, 
            startHoursToSelect, 
            endHoursToSelect, 
            startMinute, 
            endMinute, 
            user,
            frameColor,
            note,
            isEmpty, 
            handleInputChange, 
            handleSelectChange,
            handleAdd,
            handleCancel,
            handleDelete,
            handleEdit
        } = props.props
    return (
        <div id='scheduleModifier' className={'scheduleModifier ' + modifierStatus} >
            <div id='timeDuration'>
                {/* start hour+minute, end hour+minute, 
                hour cannot pass timeFrame perimiter
                minutes cannot bigger than 60
                end time cannot less than start time(endTime - StartTime should > 0) */}
                <select id='startHour' className='timeInput' onChange={handleSelectChange}>
                    {startHoursToSelect}
                </select>
                <label>:</label>
                <select id='startMinute' className='timeInput' onChange={handleSelectChange}>
                    <option value='00'>00</option>
                    {startMinute == 30 
                    ?
                    <option value='30' selected>30</option>
                    :
                    <option value='30'>30</option>
                    }
                    
                </select>
                {/* <select id='startMinute' className='timeInput' >
                    <option value='AM'>AM</option>
                    <option value='PM'>PM</option>
                </select> */}
                <label> -- </label>
                <select id='endHour' className='timeInput' onChange={handleSelectChange}>
                    {endHoursToSelect}
                </select>
                <label>:</label>
                <select id='endMinute' className='timeInput' onChange={handleSelectChange}>
                    <option value='00'>00</option>
                    {endMinute == 30 
                    ?
                    <option value='30' selected>30</option>
                    :
                    <option value='30'>30</option>
                    }
                </select>
                {/* <select id='startMinute' className='timeInput' >
                    <option value='AM'>AM</option>
                    <option value='PM'>PM</option>
                </select> */}
            </div>
            <div id='userInfo'>
                {/* userName cannot be empty, note can be empty */}
                <div className='displayRow'>
                    <div className='col-25'>
                        <label for='user'>User: </label>
                    </div>
                    <div className='col-75'>
                        <input id='user' value={user} onChange={handleInputChange} type='text' placeholder='Customer Name'></input>
                    </div>
                </div>

                <div className='displayRow'>
                    <div className='col-25'>
                        <label for='note'>Note: </label>
                    </div>
                    
                    <div className='col-75'>
                        <textarea id='note' value={note} onChange={handleInputChange}></textarea>
                    </div>
                </div>
            </div>
            <input type='color' className='col-100' id='frameColor' value={frameColor}  onChange={handleInputChange} />
            <div id='buttons'>
                {/* save, delete, cancel for edit
                add, cancel for create */}
                <button id='addSchedule' 
                        className={!isEmpty ? 'hidden' : 'visible'} 
                        onClick={handleAdd} >
                        Add
                </button>
                <button id='editSchedule' 
                        className={!isEmpty ? 'visible' : 'hidden'} 
                        onClick={handleEdit}>
                        save
                </button>
                <button id='deleteSchedule' 
                        className={!isEmpty ? 'visible' : 'hidden'}
                        onClick={handleDelete}>
                        Delete
                </button>
                <button id='cancelSchedule' 
                        onClick={handleCancel}>
                        Cancel
                </button>
            </div>
        </div>
    )
}

export default SimpleSchedule