import React, { Component } from 'react'

import AllProjects from '../projects/AllProjects'
import Profile from '../Profile'
import Contact from '../Contact'

const greeting = [
    `Welcome!`,
    `This is Peng's Den`,
    `Scroll down for more`,
    `Have a good day`,
    `Have a good day!`,
    `Have a good day.`,
    `Have a good day..`,
    `Have a good day...`
]

class Home extends Component {
    constructor (props){
        super(props)
        this.state = {
            greeting: greeting[0]
        }
    }
    componentDidMount(){
        this.autoGreeting = setInterval(() => {
            this.updateGreeting()
        }, 3000);
    }

    updateGreeting(){
        let idx = greeting.indexOf(this.state.greeting)
        idx = (idx+1)%greeting.length
        this.setState({...this.state, greeting: greeting[idx]})
    }

    componentWillUnmount(){
        clearInterval(this.autoGreeting)
    }
    render(){
        
        return (
            <div>
                <div className='bgm-1'>
                    <div className='caption'>
                        <span className='captionText'>{this.state.greeting}</span>
                    </div>
                    <div className="arrow down" id='about'/>
                </div>
                <div className='middleText'><Profile /></div>
                <div className='bgm-2' />
                <AllProjects />
                <div className='bgm-4' />
                {/* <div className='bgm-3' /> */}
                <Contact />
                
            </div>
        )
    }
}

export default Home