import React, { Component } from 'react'



class Header extends Component {
    constructor(props){
        super(props)
        this.state = {
            visible: false,
        }
        this.updateY = this.updateY.bind(this)
    }

    componentDidMount(){
        window.addEventListener('scroll', this.updateY)
    }

    componentWillUnmount(){
        window.removeEventListener('scroll', this.updateY)
    }
    
    updateY(){
        if(window.pageYOffset > 677){
            this.setState({...this.state, visible: true})
        } else {
            this.setState({...this.state, visible: false})
        }
    }
    render () {
        const { visible } = this.state
        console.log(`main-Header${visible ? ' header_Visible' : ' header_Invisible'}`)
        return (
            <div className={`main-Header${visible ? ' header_Visible' : ' header_Invisible'}`}>
                {/* <div className='header-Link'>
                    <div>
                    <h3><a href='#about' className={`header_Link_Text${visible ? ' HLT_Visible' : ' HLT_Invisible'}`}>
                            ABOUT
                        </a></h3>
                    </div>
                </div>
                <div className='header-Link'>
                    <div>
                    <h3><a href='#projects' className={`header_Link_Text${visible ? ' HLT_Visible' : ' HLT_Invisible'}`}>
                            PROJECTS
                        </a></h3>
                    </div>
                    <div>
                    <h3><a href='#contact' className={`header_Link_Text${visible ? ' HLT_Visible' : ' HLT_Invisible'}`}>
                            CONTACT
                        </a></h3>
                    </div>
                </div> */}
                <div className='header-Link'>
                    <div>
                    <a href='#about' className={`header_Link_Text${visible ? ' HLT_Visible' : ' HLT_Invisible'}`}>
                            ABOUT
                        </a>
                    </div>
                </div>
                <div className='header-Link'>
                    <div>
                    <a href='#projects' className={`header_Link_Text${visible ? ' HLT_Visible' : ' HLT_Invisible'}`}>
                            PROJECTS
                        </a>
                    </div>
                    <div>
                    <a href='#contact' className={`header_Link_Text${visible ? ' HLT_Visible' : ' HLT_Invisible'}`}>
                            CONTACT
                        </a>
                    </div>
                </div>
            </div>
        )
    }
}

export default Header